<template>
  <Container title="Creating Highlight">
    <form>
    <div class="row">
      <ImageUplodaer
        title="Add image"
        uploading-text="Uploading..."
        @uploadPoster="val => handleSetHighLight(val, 'image')"
        :poster="form.image"
      />
    </div>
    <label class="form-label">1) Please choose type</label>
    <div class="col-md-12">
      <div class="col-md-4">
        <el-radio
          v-model="type"
          @change="handleChoose"
          label="event"
        >
          Event highlight
        </el-radio>
      </div>
      <div class="col-md-4">
        <el-radio
          v-model="type"
          @change="handleChoose"
          label="location"
        >
          Location highlight
        </el-radio>
      </div>
    </div>
    <div class="col-md-12 search_box">
      <SelectSearch
        v-if="type"
        :type="type"
        name="parent_id"
        :options="setFoundedItems"
        :labelType="labelType"
        :initialValue="form.location_title"
        @handleSearch="handleSearch"
        @handleChange="handleSetHighLight"
      />
      <hr />
    </div>
   
    <div class="col-md-12">
      <label class="form-label"> 2) Please choose categories</label>
      <FindCategory
        @onSearch="findCategory"
        @onChange="search"
        :options="foundedCategories"
        :rendered="form.categories"
        generalKey="category"
        multiply
      />
      <hr />
    </div>
    <hr>
    <div class="col-md-12">
      <label class="form-label"> 3) Add description and title</label>
      <fg-input
        name="title"
        label="Highlight title"
        placeholder="Highlight title"
        :value="form.title"
        @changeValue="handleSetHighLight"
      />
      <fg-textarea 
        label="HighLight description"
        @changeValue="handleSetHighLight"
        name="text"
        :value="form.text"
        placeholder="Description of HighLight"
      />
      <div class="text-center">
        <button 
          type="submit"
          class="btn btn-info btn-fill btn-wd" 
          @click.prevent="submitForm"
        >
          {{id ? 'Update' : 'Create'}}
        </button>
        <router-link 
          to="/highLights/list" 
          class="btn btn-danger btn-fill btn-wd"
        >
          Back
        </router-link>
      </div>
    </div>
    <div class="clearfix"></div>
  </form>
  </Container>
</template>

<script>
import { mapActions, mapState, mapMutations, mapGetters } from 'vuex'
import { types, fields } from './TableMock.js'

export default {
  data: () => ({
    id: '',
    types,
    fields,
    subedKey: 'location',
    type: ''
  }),
  components: {
    ImageUplodaer: () => import('ui/ImageUploaderPlugin/ImageUploader.vue'),
    FindCategory: () => import('ui/FindCategory/FindCategory.vue'),
    Container: () => import('ui/Container/Container')
  },
  mounted() {
    this.id = this.$route.params.id
    if (this.id) {
      this.fetchHighLight({ id: this.id })
    } else {
      this.clearHighlightForm()
    }
  },
  watch: {
    form() {
      this.type = this.form.type
    }
  },
  computed: {
    ...mapState({
      form: state => state.highLights.highLight
    }),
    ...mapGetters([
      'setFoundedItems',
      'foundedCategories'
    ]),
    labelType() {
      return this.type === 'event' ? 'title' : 'title'
    }
  },
  methods: {
    search(value) {
      const values = Object.values(value)
      const categories = [...values]
      this.updateHighLightForm({
        key: 'categories',
        value: categories
      })
    },
    submitForm () {
      const id = this.id ? this.id : ''
      id ? this.updateHighLight({ id }) : this.createHighLight()
      this.$router.push('/highLights/list')
    },
    ...mapActions({
      fetchHighLight: 'fetchHighLight',
      createHighLight: 'createHighLight',
      updateHighLight: 'updateHighLight',
      handleSearch: 'searchTypeItem',
      findCategory: 'findCategory'
    }),
    ...mapMutations({
      resetForm: 'resetHighLight',
      setPoster: 'setPoster',
      updateHighLightForm: 'updateHighLightForm',
      clearHighLightSearch: 'clearHighLightSearch',
      clearHighlightForm: 'clearHighlightForm'
    }),
    handleChoose(value) {
      this.clearHighLightSearch()
      this.$children[0].$children[7].query = ''
      const payload = {
        value,
        key: 'type'
      }
      this.updateHighLightForm(payload)
    },
    handleSetHighLight(value, key) {
      const payload = {
        key,
        value
      }
      this.updateHighLightForm(payload)
    }
  }
}
</script>

<style>
  .search_box {
    padding-left: 0;
  }

  .form-label {
    margin-bottom: 20px!important;
  }
</style>



