const tableColumns = [
  {
    title: 'Title',
    field: 'title',
    isIcon: false,
    icon: ''
  },
  {
    title: 'Location or event',
    field: 'type',
    isIcon: false,
    icon: ''
  },
  {
    title: 'Created At',
    field: 'createdAt',
    isIcon: false,
    icon: ''
  }
]

const tableActions = [
  {
    tag: 'link',
    class: 'btn btn-info',
    label: 'Edit',
    dynamic: false,
    to: '/highlights/update',
    urlType: '_id',
    emit: ''
  },
  {
    tag: 'button',
    class: 'btn btn-danger',
    label: 'Remove',
    dynamic: false,
    urlType: '_id',
    to: '',
    emit: 'clickDelete'
  }
]

const types = [
  {
    name: 'events',
    value: 'event',
    _id: 'event'
  },
  {
    name: 'locations',
    value: 'location',
    _id: 'location'
  },
  {
    name: 'highlight',
    value: 'highlight',
    _id: 'highlight'
  }
]

const fields = [
  {
    name: 'title',
    label: 'Highlight title',
    placeholder: 'Highlight title',
    onChange: 'handleSetHighlight',
    value: 'title',
    type: 'fg-input'
  }
]

export {
  tableColumns,
  tableActions,
  types,
  fields
}



